<script>
    export default {
        data() {
            return {
                modals: {
                    modal1: false,
                    modal2: false,
                    modal3: false
                },
                switches: {
                    off: false,
                    on: true
                },
                radio: {
                    radio1: "radio1",
                    radio2: "radio3"
                },
                dates: {
                    simple: "2018-07-17",
                    range: "2018-07-17 to 2018-07-19"
                },
                selects: {
                    simple: '',
                    languages: [{value: 'Bahasa Indonesia', label: 'Bahasa Indonesia'},
                        {value: 'Bahasa Melayu', label: 'Bahasa Melayu'},
                        {value: 'Català', label: 'Català'},
                        {value: 'Dansk', label: 'Dansk'},
                        {value: 'Deutsch', label: 'Deutsch'},
                        {value: 'English', label: 'English'},
                        {value: 'Español', label: 'Español'},
                        {value: 'Eλληνικά', label: 'Eλληνικά'},
                        {value: 'Français', label: 'Français'},
                        {value: 'Italiano', label: 'Italiano'},
                        {value: 'Magyar', label: 'Magyar'},
                        {value: 'Nederlands', label: 'Nederlands'},
                        {value: 'Norsk', label: 'Norsk'},
                        {value: 'Polski', label: 'Polski'},
                        {value: 'Português', label: 'Português'},
                        {value: 'Suomi', label: 'Suomi'},
                        {value: 'Svenska', label: 'Svenska'},
                        {value: 'Türkçe', label: 'Türkçe'},
                        {value: 'Íslenska', label: 'Íslenska'},
                        {value: 'Čeština', label: 'Čeština'},
                        {value: 'Русский', label: 'Русский'},
                        {value: 'ภาษาไทย', label: 'ภาษาไทย'},
                        {value: '中文 (简体)', label: '中文 (简体)'},
                        {value: 'W">中文 (繁體)', label: 'W">中文 (繁體)'},
                        {value: '日本語', label: '日本語'},
                        {value: '한국어', label: '한국어'}],
                    multiple: 'ARS'
                },
                options: [],
                value: [],
                list: [],
                loading: false,
                states: ["Alabama", "Alaska", "Arizona",
                    "Arkansas", "California", "Colorado",
                    "Connecticut", "Delaware", "Florida",
                    "Georgia", "Hawaii", "Idaho", "Illinois",
                    "Indiana", "Iowa", "Kansas", "Kentucky",
                    "Louisiana", "Maine", "Maryland",
                    "Massachusetts", "Michigan", "Minnesota",
                    "Mississippi", "Missouri", "Montana",
                    "Nebraska", "Nevada", "New Hampshire",
                    "New Jersey", "New Mexico", "New York",
                    "North Carolina", "North Dakota", "Ohio",
                    "Oklahoma", "Oregon", "Pennsylvania",
                    "Rhode Island", "South Carolina",
                    "South Dakota", "Tennessee", "Texas",
                    "Utah", "Vermont", "Virginia",
                    "Washington", "West Virginia", "Wisconsin",
                    "Wyoming"]
            }
        },
        mounted() {
            this.list = this.states.map(item => {
                return {value: `value:${item}`, label: `label:${item}`};
            });
        },
        methods: {
            remoteMethod(query) {
                if (query !== '') {
                    this.loading = true;
                    setTimeout(() => {
                        this.loading = false;
                        this.options = this.list.filter(item => {
                            return item.label.toLowerCase()
                                .indexOf(query.toLowerCase()) > -1;
                        });
                    }, 200);
                } else {
                    this.options = [];
                }
            },
            notifyTopRight() {
                this.$notify({verticalAlign: 'top', horizontalAlign: 'right', message: 'Top right'});
            },
            notifyTopLeft() {
                this.$notify({verticalAlign: 'top', horizontalAlign: 'left', message: 'Top left'});
            },
            notifyTopCenter() {
                this.$notify({verticalAlign: 'top', horizontalAlign: 'center', message: 'Top center'});
            },
            notifyBottomLeft() {
                this.$notify({verticalAlign: 'bottom', horizontalAlign: 'left', message: 'Bottom left'});
            },
            notifyBottomCenter() {
                this.$notify({verticalAlign: 'bottom', horizontalAlign: 'center', message: 'Bottom center'});
            },
            notifyBottomRight() {
                this.$notify({verticalAlign: 'bottom', horizontalAlign: 'right', message: 'Bottom center'});
            }
        }
    };
</script>
